<template>
  <div v-if="detail"  >

    <div  class="pic"
        
    >
    <div class="mask"
    :style="{
        backgroundImage: `url(${detail.coverImgUrl}?imageView-1&type=webp&thumbnail=246x0) `,
      }"
    >

    </div>
    <div class="picp"><img :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=246x0`" alt="">
    <span
    class="bofang"
        ><img
          style="width: 10px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{detail.playCount | parseCount }}</span
      ><span class="ss">歌单</span>
    </div>

    <div class="pict" >
      <h2 class="pic-title">{{detail.name}}</h2>
    <img :src="`${detail.creator.avatarUrl}?imageView=1&type=webp&thumbnail=246x0`" alt="">
    <span>{{detail.creator.nickname}}</span>
    </div>
      
    </div>
    <div class="pl-title">歌曲列表</div>
 <ul class="list">
      <SongListItem 
      v-for="(item,index) in detail.tracks" 
      :key="item.id"
      :item="item"
      @change-current-song="
      $emit('change-current-song',item);
      $emit('change-current-play-list',detail.tracks) ;"
      :currentSongId="currentSongId"
      :playing="playing"
      :class="{lt3:index<3}"
      >{{ index+1}}</SongListItem>
    </ul>
    <div class="pl-title">最新评论</div>
    <ul class="pl-list">
    <li
    v-for="(item, index) in pllist" :key="index"
    >
    <div class="pic"
   
    >
 <img
          style="width: 100%"
          :src="`${item.user.avatarUrl}?imageView=1&type=webp&thumbnail=246x0`" alt=""
          
        />
    </div>
    <div class="text">
      <div class="text1">{{item.user.nickname}}</div>
      <div class="text2">
       
          {{item.time| contenttime }}
      </div>
      <div class="text3"> <div 
            v-show="item.beReplied[0]"
            >回复 <strong style="color:lightblue;list-style:none;">
              @{{item.beReplied[0]?item.beReplied[0].user.nickname: ""}}</strong>：
        </div>{{item.content}}</div>
         <div class="beReplied"
    v-show="item.beReplied.length>=1"
    >
    <strong style="color:lightblue;list-style:none;">
              @{{item.beReplied[0]?item.beReplied[0].user.nickname: ""}}</strong>：
              {{item.beReplied[0]?item.beReplied[0].content: ""}}
   
    </div>
    </div>
   
    
    </li>
  </ul>
  </div>
 
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },

    playing: Boolean,
  },
  data: function () {
    return {
      detail: null,
      pllist: null,
    };
  },
  filters: {
    parseCount: function (value) {
      // return value + "0";
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
    contenttime: function (value) {
      var m = "";
      var date = 0;
      if (Date.now() - value < 3600 * 1000) {
        date = new Date(Date.now() - value);
        m = date.getMinutes() + "分钟前 ";

        // console.log(m);
      } else if (Date.now() - value < 3600 * 24 * 1000) {
        date = new Date(Date.now() - value);
        m = date.getHours() + "小时前";
        // console.log(m);
      } else if (Date.now() - value < 3600 * 24 * 7 * 1000) {
        date = new Date(Date.now() - value);
        m = date.getDate() + "日前 ";
        // console.log(m);
      } else {
        // console.log(Date.now()-value)
        date = new Date(value);
        m =
          (date.getFullYear() < 10
            ? "0" + date.getFullYear()
            : date.getFullYear()) +
          "年 " +
          ((date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1) +
            "月 ") +
          (date.getDate() + "日 ");
        //  console.log(m)
      }
      return m;
    },
  },
  
  created: function () {
    this.getPlayListDetail(this.$route.query.id);
    this.getPlayListComment(this.$route.query.id);
  },
  methods: {
    getPlayListDetail: function (id) {
      this.axios
        .get("/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          console.log(res);
          this.detail = res.data.playlist;
          console.log(id);
        });
    },
    getPlayListComment: function (id) {
      this.axios
        .get("/comment/playlist", {
          params: {
            id,
          },
        })
        .then((res) => {
          console.log(res);
          this.pllist = res.data.comments;

          // console.log(this.pllist.length);
          // console.log(this.pllist.user.nickname);
          // console.log(this.pllist.content);
          // console.log(this.pllist.beReplied);
        });
    },
  },
  beforeRouteEnter (to, from, next) {
    next((vn)=>{
        vn.getPlayListDetail(vn.$route.query.id);
        vn.getPlayListComment(vn.$route.query.id);
    })
  }
  
    
  
};
</script>

<style lang="less" scoped>
.pl-title {
  padding: 0 10px;
  height: 23px;
  line-height: 23px;
  margin: 0;
  padding: px 10px;
  color: #666;
  font-size: 12px;
  font-weight: 400;
  background: rgba(0, 0, 0, 0.05);
}
.pl-list {
  width: 100%;
  li {
    width: 100%;
    float: left;
    border-bottom: lightgray 1px solid;
    margin-bottom: 3px;
    .pic {
      float: left;
      width: 7%;
      height: auto;

      margin: 0 10px;
      img {
        border-radius: 50%;
        margin-top: 10px;
        max-width: 40px;
      }
    }
    .text {
      list-style: none;
      display: block;
      width: 80%;
      overflow: hidden;
      float: left;
      div {
        margin-bottom: 3px;
      }
      .text1 {
        font-size: 12px;
      }
      .text2 {
        font-size: 10px;
        color: #999;
      }
      .text3 {
        font-size: 14px;
        margin: 5px 0;
      }
      .beReplied{
        padding: 3px;
        font-size: 14px;
        border:rgb(228, 227, 227) 1px solid;
      }
    }
  }
}
.pic {
  width: 100%;
  height: 200px;
  list-style: none;
  background-size: 100%;
  overflow: hidden;
  position: relative;
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    filter: blur(30px) brightness(0.8);
    z-index: -1;
  }
  .picp {
    margin-top: 10px;
    float: left;
    width: 40%;
    position: relative;
    img {
      width: 100%;
      vertical-align: middle;
    }

    .ss {
      position: absolute;
      top: 10px;
      left: 0;
      padding: 0 8px;
      height: 17px;
      color: #fff;
      font-size: 9px;
      text-align: center;
      line-height: 17px;
      background-color: rgba(217, 48, 48, 0.8);
      border-top-right-radius: 17px;
      border-bottom-right-radius: 17px;
    }
    .bofang {
      position: absolute;
      right: 2px;
      top: 0;
      font-size: 10px;
      color: white;
    }
  }
  .pict {
    width: 50%;
    float: left;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    color: white;
    .pic-title {
      font-size: 16px;
      line-height: 16px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    img {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      vertical-align: middle;
      margin-right: 3px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    span {
      line-height: 30px;
      font-size: 10px;
      color: rgb(223, 215, 215);
    }
  }
}
ul {
  padding-inline-start: 0%;
}
.play-list {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    top: 0;
  }
  .swiper {
    width: 100vw;
    height: 60vh;
    position: absolute;
    left: 0;
    bottom: 10px;
    padding: 0 10px;
    .card {
      background-color: white;
      height: 100%;
      border-radius: 15px;
      padding: 15px 0;
      ul.list {
        height: 100%;
        overflow: auto;
      }
    }
  }
}
</style>
