<template>
  <div class="search">
    <div class="Search">
      <i></i>
      <input
        type="text"
        placeholder="搜索热门歌曲,歌手"
        v-model.trim="value"
        @keyup.enter="value && (serarching = true)"
        @focus="inputing = true"
        @blur="inputing = false"
        class="sou"
      />
      <i class=""></i>
     
    </div>
 <section class="sugguests" v-if="value && !searching">
      <h5>搜索建议</h5>
      <ul>
        
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
          :style="{
        background: `${index % 2==0 ? red : blue}`,
      }"
        >
          {{ item.keyword }}

          
        </li>
      </ul>
    </section>
    <section class="hot" v-if="!value && !searching">
      <h3 class="hot">热门搜索</h3>
      <div class="f">
        <ul class="tophot">
          <li
            class="hotkey"
            v-for="hot in hots"
            :key="hot.first"
            @click="
              searching = true;
              value = hot.first;
            "
          >
            {{ hot.first }}
          </li>
        </ul>
      </div>

      <h3 class="historytip">历史记录</h3>
      <ol>
        <li
          v-for="(h,index) in history"
          :key="h"
         
        >
        <em
         @click="
            searching = true;
            value = h;
          "
        >{{ h }}</em>
          
          <div class="iconfont icon-shanchu" @click="dhistory(index)"></div>
        </li>
      </ol>
    </section>
    
    <section class="suggests" v-if="searching">
      <h5>搜索结果</h5>

      <ul>
          <SongListItem
          v-for="item in searchResults"
          :key="item.id"
          :item="item"
          @change-current-song="
            $emit('change-current-song', $event);
            $emit('change-current-play-list', searchResults);
          "
          :currentSongId="currentSongId"
          :playing="playing"
        ></SongListItem>
        <!-- <li v-for="(item, index) in searchResults" :key="index">
          {{ item.name }}
        </li> -->
      </ul>
<div class="djgd" @click="getSearch" v-if="hasMore">点击跟多</div>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";

export default {
  name: "Serach",
  components: {
    SongListItem,
  },
  
   props:{
    currentSongId:{
      type:Number,
    },
    playing:Boolean,
  },
  data: function () {
    return {
      hots: [],
      serachkey: [],
      suggests: [],
      searchResults: [],
      value: "",
      searching: false,
      inputing: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      red:"lightgray",
      blue:"white",
      fn:"",
    };
  },

  methods: {
    // scroll: function (event) {
    //   if (this.hasMore) {
    //     if (
    //       event.target.offsetHeight + event.target.srollTop ===
    //       event.target.scrollHeight
    //     ) {
    //       console.log("触底");
    //       this.getSearch();
    //     }
    //   } else {
    //     console.log("没有更多了");
    //   }
    // },
    dhistory:function(index){
      console.log(index);
      this.history.splice(index,1);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
    getSearch: function () {
      this.axios
        .get("/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.page * 30,
          },
        })
        .then((res) => {
          this.searchResults.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
          console.log(this.searchResults);
          // console.log(!this.searchResults.song);
        });
      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
  created: function () {
    this.axios.get("/search/hot").then((res) => {
      //   console.log(this, res.data.result.hots);
      this.hots = res.data.result.hots;
    });
  },
  watch: {
    value: function (n) {
      clearTimeout(this.fn);
     this.fn= setTimeout(() => {
        if (this.inputing) {
        this.searching = false;
      }
      if (n && !this.searching) {
        this.axios
          .get("/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
      }
      }, 1000);
      
    },
    
    searching: function (n) {
      if (n && this.value) {
        console.log("触底");
        this.getSearch();
      } else {
        this.searchResults = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  background: #fcfcfd;
  .sou {
    width: 90%;
    height: 30px;
    line-height: 30px;
    border-radius: 20px;
    text-indent: 20px;
  }
  .Search {
    padding: 10px;
    position: relative;
    h5{
      margin-left:5px ;
    }
    .hot {
      ul {
        overflow: hidden;
        li {
          float: left;
          border: 1px solid red;
          margin: 5px;
        }
        
      }
    }
   .sugguests{
     width: 100%;
     position: absolute;
     top: 80px;
    border-top: none;
    padding-top: 20px;
    margin-top: -17px;
ul {
         width: 100%;
        
        li {
          padding:  5px 0;
        }
  }}
  }
  
   ol {
      overflow: hidden;
      li {
        display: block;
        width: 100%;
        height: 30px;
        line-height: 30px;
        float: left;
        padding-left: 10px;
        border-bottom: 1px solid cadetblue;
        
        em{
          width: 80%;
        }
          div{
            float: right;
    margin-right: 10px;
          }
      }
    }
  .historytip {
    line-height: 12px;
    font-size: 12px;
    color: #666;
    border-bottom: 1px solid cadetblue;}
  .hot {
    line-height: 12px;
    font-size: 12px;
    color: #666;
    .f {
      display: inline-block;
    }
  }
  .tophot {
    width: 100%;
    padding-inline-start: 0%;
    .hotkey {
      list-style: none;
      float: left;
      border: 1px solid rgb(63, 56, 56);
      padding: 5px;
      margin: 3px;
      border-radius: 20px;
    }
  }
  .djgd{
    width: 100%;
    height: 80px;
    line-height: 80px;
    text-align: center;
  }
}
</style>