<template>
  <section class="play-page" 
  >
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song ? currentSong.picUrl : currentSong.al ? currentSong.al.picUrl : currentSong.album.artist.img1v1Url
        }?imageView-1&type=webp&thumbnail=246x0)`,
      }"
    ></div>
    <div class="iconfont icon-xiala1" 
    @click="$emit('toggle-show-play-page', false)"></div>
    <!-- <button @click="$emit('toggle-show-play-page', false)">play-page</button> -->

    <section class="rotate">
      <img
        class="needle"
        :class="{ paused: !playing }"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
        alt=""
      />
      <section class="record" :class="{ playing: playing }">
        <img
          class="thumb"
          :src="currentSong.song ? currentSong.picUrl : currentSong.al ? currentSong.al.picUrl : currentSong.album.artist.img1v1Url"
          alt=""
        />
        <img
          class="disc"
          src="https://s3.music.126.net/mobile-new/img/disc.png"
          alt=""
        />
        
      </section>
     
    </section>
     <div class="box">
 <ul class="content"
    ref="lyricContent"
    v-if="parsedLyric.length"
    :style="{marginTop: -scrollH +'px'}">
    <li v-for="( l , i ) in parsedLyric" :key="i">
      <span
      :style="{animationDuration: parsedLyric[i+1] 
      ? parsedLyric [ i + 1 ].time  - l.time - 0.5 + 's' : '3s',}"
      :class="{ active:currentLyricIndex ===i,
      playing:playing&& currentLyricIndex ===i,}"
      >
      {{l.text}}
      </span>
    </li>
    </ul>
      </div>
   
     <!-- <div class="box " >
       <div></div>
          <ul>
          </ul>
        </div> -->
        <section class="progress">
          <input type="range"
          :max="duration"
          step="0.5"
          v-model="value"
          @change="progressChange"
          @input="progressInput"
          class="boft"
          />
          <span
          class="bar"
          :style="{width:( value / duration * 100 + '%')}">

          </span>
        </section>
    <section class="controls">
     
      <span @click="$emit('toggle-play-model')" class="iconfont icon-moshi"></span>
      <span @click="$emit('prev-song')" class="iconfont icon-diyiye" ></span>
      <span v-if="playing" class="iconfont icon-zanting" @click="$emit('toggle-playing-state')"  ></span>
      <span v-else class="iconfont icon-bofang" @click="$emit('toggle-playing-state')" ></span>
      <span @click="$emit('next-song')" class="iconfont icon-zuihouyiye" ></span>
      <span @click.stop="$emit('toggle-show-play-list', true)" class="iconfont icon-lisimoshicaidandaohang"></span>
    </section>
  </section>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    
  },
  data: function () {
    return {
      plc: "",
      value:this.currentTime,
      inputing:false,
      showLyric:false,
      lyric:null,
      lisH:[],
      scrollH:0,
    };
  },
  watch:{
    currentTime:function(n){
      //  console.log(n);
      if(!this.inputing){
        this.value=n;
        // console.log(this.value);
      }
    },
    "currentSong.id":function(id){
      this.getLyric(id);
    },
    currentLyricIndex:function(index){
      var h = this.lisH.slice(0, index).reduce(function(total, item) {
        return total + item;
      }, 0);
      console.log(h);
      h = h > 0 ? h - 0 : 0;
      h = h > 1928 ? 1928 : h;
      this.scrollH = h;
    },
    parsedLyric:function(){
      this.$nextTick(()=>{
        var lis  = this.$refs.lyricContent.querySelectorAll("li");
        this.lisH = [...lis].map((item)=>item.offsetHeight);
      });
    },
  },
  
  methods: {
     progressChange: function(event) {
      this.inputing = false;
      this.$emit("current-time-change", event.target.value);
      console.log(this.currentTime);
      console.log(event.target.value);
    },
    progressInput: function() {
      this.inputing = true;
    },
    getLyric:function(id){
      this.axios.get("/lyric",{
        params:{id},

      })
      .then((res)=>(this.lyric=res.data.lrc.lyric));
    },
  },
  created: function() {
    this.getLyric(this.currentSong.id);
      
  },
  computed: {
    parsedLyric: function() {
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            var text = s.replace(/^\[\d{2}:\d{2}\.\d{2,3}\]/i, "");
            text==""?text="......":text;
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            var timeArr = timeStr.split(":").map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
    currentLyricIndex: function() {
     var i = this.parsedLyric.findIndex(
        (item) => item.time > this.currentTime
      );
      var currentLyricIndex = i !== -1 ? i - 1 : this.parsedLyric.length - 1;
      return currentLyricIndex;
    },
  },
};

</script>

<style lang="less" scoped>
.progress{
  width: 90vw;
  height: 4px;
  margin: 20px auto;
  position: relative;
  border-radius: 10px;
}
.box {
  text-align: center;
  width: 100%;
  height: 120px;
  overflow: hidden;
 color: white;
        /* padding: 20px; */
      }
      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        overflow: hidden;
        transition: all 0.3s;
        transform: translateY(20px);
      }
      ul li {
        padding: 2px;
        line-height: 21px;
      }

    

      @keyframes xxx {
        from {
          background-position-x: 0;
        }
        to {
          background-position-x: -100%;
        }
      }
      .active {
        font-size: 1.2rem;
        animation-name: xxx;
        animation-timing-function: linear;
        animation-duration: 3s;
        background-image: linear-gradient(to right, red 50%, blue 50%);
        background-size: 200%;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }


.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top:-2px;
  overflow: hidden;
  .icon-xiala1{
    font-size: 30px;
    color: white;
    margin-left: 3px;
  }
  &::before {
    content: "";
    display: block;
    .pos-ab();
    z-index: -2;
    background-color: #333;
  }
  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.8);
    .pos-ab();
    z-index: -1;
    transform: scale(1.5);
    transition: all 0.3s;
  }
  .rotate {
    position: relative;
    padding-top: 25vw;
    img.needle {
      height: 40vw;
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 9;
      margin-left: -15px;
      transform-origin: 15px 15px;
      transform: rotate(0deg);
      transition: all 0.3s;
      &.paused {
        transform: rotate(-20deg);
      }
    }
    .record {
      position: relative;
      width: 80vw;
      height: 80vw;
      margin: 0 auto 0 auto;
      img {
        .pos-ab();
        border-radius: 50%;
        &.thumb {
          transform: scale(0.8);
        }
      }
      animation: rotate 8s linear infinite;
      animation-play-state: paused;
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .boft{
    width: 100%;
    height: 4px;
    position: absolute;
    top: 0;
    left: 0;
  }
  .controls {
    display: flex;
    justify-content: space-around;
    color: white;
    span{
        font-size: 34px;
        line-height: 34px;
    }
    
  }
}
</style>